import type { Metadata } from "next";
import { notFound } from "next/navigation";
import Link from "next/link";
import { CITY_PROFILES, getCityProfile } from "@/data/cities";
import { getStateByAbbr } from "@/data/states";
import { RoundSkyWidget } from "@/components/round-sky-widget";
import { TcpaConsent } from "@/components/compliance/tcpa-consent";
import { FaqList } from "@/components/faq/faq-list";

export const dynamicParams = false;

export function generateStaticParams() {
  return CITY_PROFILES.map((city) => ({
    state: city.stateAbbr.toLowerCase(),
    city: city.slug,
  }));
}

export async function generateMetadata({
  params,
}: {
  params: Promise<{ state: string; city: string }>;
}): Promise<Metadata> {
  const { state: stateParam, city } = await params;
  const cityProfile = getCityProfile(stateParam, city);
  const state = getStateByAbbr(stateParam);
  if (!cityProfile || !state) return { title: "City not found" };

  const title = `Loans in ${cityProfile.city}, ${state.abbr}`;
  const description = `Compare installment, payday alternatives, and personal loan options for borrowers in ${cityProfile.city}, ${state.abbr}. Includes APR estimates and compliance notes for ${state.name}.`;

  return {
    title,
    description,
    alternates: {
      canonical: `https://www.loanaiportal.com/cities/${state.abbr.toLowerCase()}/${cityProfile.slug}`,
    },
    openGraph: {
      title,
      description,
      type: "article",
    },
  };
}

